import Vue from "vue";
import Vuex from 'vuex';
import axios from "axios";
import {
  API_BASE_URL
} from "@/config";



Vue.use(Vuex); // сообщаем Vue что будем использовать vuex

export default new Vuex.Store({ // создадим и экспортируем новый экземпляр хранилища
  state: { // в свойстве состояния будем хранить массив продуктов состоящий из:
    cartProducts: [],

    userAccessKey: null, // хранение уникального ключа пользователя

    cartProductsData: [],

    orderInfo: null, // здесь хранится информация о заказе

  },

  mutations: {

    updateOrderInfo(state,
      orderInfo
    ) { // с помощью метода будем записывать информацию о заказе в глобальное хранилище
      state.orderInfo = orderInfo;
    },

    resetCart(state) { // метод очистки корзины в глобальном хранилище
      state.cartProducts = [];
      state.cartProductsData = [];
    },

    updateCartProductAmount(state, {
      productId,
      amount
    }) {
      const item = state.cartProducts.find(item => item.productId === productId);
      if (item) {
        item.amount = amount;
      }
    },

    deleteCartProduct(state, productId) {
      state.cartProducts = state.cartProducts.filter(item => item.productId != productId);
    },

    updateUserAccessKey(state, accessKey) {
      state.userAccessKey = accessKey;
    },

    updateCartProductsData(state, items) {
      state.cartProductsData = items;
    },

    syncCartProducts(state) {
      state.cartProducts = state.cartProductsData.map(item => {
        return {
          productId: item.product.id,
          amount: item.quantity,
        }
      })
    }
  },

  getters: { //геттеры, похоже на вычисляемое свойство
    cartDetailProduct(state) { //вычисляемая функция о детальной информации о товарах в корзине
      return state.cartProducts.map(
        item => { // обращаемся к массиву продуктов в состоянии (cartProducts) и для каждого item подмешаем информацию
          const product = state.cartProductsData.find(p => p.product.id === item.productId)
            .product;
          return {
            ...item,
            product: {
              ...product,
              image: product.image.file.url,

            },
          }
        });
    },

    cartTotalPrice(state, getters) {
      return getters.cartDetailProduct.reduce((acc, item) => (item.product.price * item
        .amount) + acc, 0);
    }
  },

  actions: {
    async loadOrderInfo(context,
      orderId) { // действие которые будет загружать данные о заказе по его индификатору
      return axios.get(API_BASE_URL + '/api/orders/' + orderId, {
          params: {
            userAccessKey: context.state
              .userAccessKey // состояние в глоб.хранилище - уникальный ключ пользователя
          }
        })
        .then(response => {
          localStorage.setItem('orderInfo', JSON.stringify(response.data));
          context.commit('updateOrderInfo', response.data); // при успешном запросе вызовем мутацию и заполним orderInfo данными о заказе
        })
    },

    async loadCart(context) {
      return axios.get(API_BASE_URL + '/api/baskets', {
          params: {
            userAccessKey: context.state
              .userAccessKey // состояние в глоб.хранилище - уникальный ключ пользователя
          }
        })
        .then(response => {
          if (!context.state.userAccessKey) {
            localStorage.setItem('userAccessKey', response.data.user
            .accessKey); // Запоминаем уникальный ключ пользователя
            context.commit('updateUserAccessKey', response.data.user.accessKey);
            context.commit('updateOrderInfo', response.data);
          }
          context.commit('updateCartProductsData', response.data.items);
          context.commit('syncCartProducts');

        })
    },

    async addProductToCart(context, {
      productId,
      amount
    }) {
      return (new Promise(resolve => setTimeout(resolve, 2000))).then(async () => {
        return axios.post(API_BASE_URL + '/api/baskets/products', {
            productId: productId,
            quantity: amount
          }, {
            params: {
              userAccessKey: context.state.userAccessKey
            }
          })
          .then(response => {
            context.commit('updateCartProductsData', response.data.items);
            context.commit('syncCartProducts');
          })
      })
    },

    async updateCartProductAmount(context, {
      productId,
      amount
    }) {
      context.commit('updateCartProductAmount', {
        productId,
        amount
      });
      if (amount < 1) {
        return;
      }
      return axios.put(API_BASE_URL + '/api/baskets/products', {
          productId: productId,
          quantity: amount
        }, {
          params: {
            userAccessKey: context.state.userAccessKey
          }
        })
        .then(response => {
          context.commit('updateCartProductsData', response.data.items);
        })
        .catch(() => {
          context.commit('syncCartProducts');
        })
    },

    async deleteProductToCart(context, productId) {
      context.commit('deleteCartProduct', productId);
      return axios.delete(API_BASE_URL + '/api/baskets/products', {
          data: {
            productId: productId
          },
          params: {
            userAccessKey: context.state.userAccessKey
          }
        })
        .then(response => {
          context.commit('updateCartProductsData', response.data.items);
          context.commit('syncCartProducts');
        })
    }
  }
});
