<template>
 <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name:'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name:'cart'}">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'order'}">
            Оформление заказа
          </router-link>
        </li>
      </ul>

      <h1 class="content__title">
        Заказ оформлен <span>№ 23621</span>
      </h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
            Благодарим за&nbsp;выбор нашего магазина. На&nbsp;Вашу почту придет письмо с&nbsp;деталями заказа.
            Наши менеджеры свяжутся с&nbsp;Вами в&nbsp;течение часа для уточнения деталей доставки.
          </p>

          <ul class="dictionary">
            <li class="dictionary__item">
              <span class="dictionary__key">
                Получатель
              </span>
              <span class="dictionary__value">
             {{orderInfo.name}}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Адрес доставки
              </span>
              <span class="dictionary__value">
               {{orderInfo.address}}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Телефон
              </span>
              <span class="dictionary__value">
               {{orderInfo.phone}}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Email
              </span>
              <span class="dictionary__value">
                {{orderInfo.email}}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Способ оплаты
              </span>
              <span class="dictionary__value">
                картой при получении
              </span>
            </li>
          </ul>
        </div>

        <div class="cart__block">
    <ul class="cart__orders">
      <li class="cart__order"  v-for="item in products" :key="item.product.id" >
        <h3>{{item.product.title}}</h3>
        <b>{{item.product.price | numberFormat}} ₽</b>
        <span>Артикул: {{ item.product.id }}</span>
      </li>

    </ul>

    <div class="cart__total">
      <p>Доставка: <b>{{ delivery }} ₽</b></p>
      <p v-if="products.length===1">Итого: <b>{{ products.length }}</b> товар на сумму <b>{{ orderInfo.totalPrice + delivery | numberFormat }} ₽</b></p>
      <p v-else="products.length > 1">Итого: <b>{{ products.length }}</b> товара на сумму <b>{{ orderInfo.totalPrice + delivery | numberFormat }} ₽</b></p>
    </div>

  </div>

      </form>
    </section>
  </main>
</template>

<script>


import numberFormat from '@/helpers/numberFormat';
import { mapState } from 'vuex';
// import { mapGetters } from 'vuex';

export default {
data(){
  return {
delivery: 500,
  }
},
filters: {numberFormat},

  computed: {
    products(){
    return  this.$store.state.orderInfo.basket.items
    },
    ...mapState(['orderInfo']),
  },


created(){
  if(this.$store.state.orderInfo && this.$store.state.orderInfo.id ===  this.$route.params.id ){ // если информация о заказе содержится в состоянии хранилища и совпадают индификаторы динамических сегментов мы будем останавливать ХУК
    return ;
  }
  this.$store.dispatch('loadOrderInfo', this.$route.params.id);
}
}
</script>
