<template>
  <div class="cart__block">
    <ul class="cart__orders">
      <li class="cart__order"  v-for="item in products" :key="item.product.id" >
        <h3>{{item.product.title}}</h3>
        <b>{{item.product.price | numberFormat}} ₽</b>
        <span>Артикул: {{ item.product.id }}</span>
      </li>

    </ul>

    <div class="cart__total">
      <p>Доставка: <b>{{ delivery }} ₽</b></p>
      <p v-if="products.length===1">Итого: <b>{{ products.length }}</b> товар на сумму <b>{{ totalPrice + delivery | numberFormat }} ₽</b></p>
      <p v-else="products.length > 1">Итого: <b>{{ products.length }}</b> товара на сумму <b>{{ totalPrice + delivery | numberFormat }} ₽</b></p>
    </div>

  </div>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import { mapGetters } from 'vuex';

export default {
  data(){
return {
  delivery: 500,
}
  },

  props:['item'],
  filters: {numberFormat},
  computed: {
    ...mapGetters({ products: 'cartDetailProduct', totalPrice: 'cartTotalPrice' }),
  }
}
</script>
