<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link  class="breadcrumbs__link" :to="{name: 'cart'}">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info" v-if="products.length === 1">
      {{ products.length }} товар
      </span>
      <span class="content__info" v-else="products.length > 1" >
      {{ products.length }} товара
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">

            <BaseFormText title="ФИО" :error="formError.name" placeholder="Введите ваше полное имя"
              v-model="formData.name" />

            <BaseFormText title="Адрес доставки" :error="formError.address" placeholder="Введите ваш адрес"
              v-model="formData.address" />

            <BaseFormText title="Телефон" :error="formError.phone" placeholder="Введите ваш телефон"
              v-model="formData.phone" />

            <BaseFormText title="Email" :error="formError.email" placeholder="Введи ваш Email" v-model="formData.email" />

            <BaseFormTextArea title="Комментарий к заказу" :error="formError.comment" v-model="formData.comment"
              placeholder="Ваши пожелания">
              <textarea class="form__input form__input--area"></textarea>
            </BaseFormTextArea>

          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="0" checked="">
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                  <span class="options__value">
                    Курьером <b>500 ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card">
                  <span class="options__value">
                    Картой при получении
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                  <span class="options__value">
                    Наличными при получении
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">

          <OrderInfoPageProducts  />

    <button class="cart__button button button--primery" type="submit" >
      Оформить заказ
    </button>
  </div>

        <!-- блок покажется при возникновении ошибки -->
        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
          {{ formErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import BaseFormText from '@/components/BaseFormText.vue';
import BaseFormTextArea from '@/components/BaseFormTextArea.vue';
import OrderInfoPageProducts from '@/components/OrderInfoPageProducts.vue';
import { API_BASE_URL } from '@/config';
import axios from 'axios';

export default {
  components: { BaseFormText, BaseFormTextArea, OrderInfoPageProducts },
  data() {
    return {
      formData: {}, // объект свойств
      formError: {}, // объект ошибок
      formErrorMessage: '', // сообщение об ошибке
      products:  this.$store.state.cartProductsData,

    }
  },
computed: {
showBtn(){
this.products.length != 0
}
},

  methods: {
    order(){ // отправляем post запрос на сервер
this.formError = {},// очищаем поля ошибок после успешного отпраления формы
this.formErrorMessage = '', // очищаем поле с ошибками при  отправке формы

axios.post(API_BASE_URL + '/api/orders', {
  ...this.formData       //отправляем данные формы на сервер
}, {
  params: {    // в 3 аргументе передаем параметры уникального ключа пользователя
   userAccessKey: this.$store.state.userAccessKey // который хранится в глобальном хранилище
  }
})
.then((response) => {
  this.$store.commit('resetCart'); // при успешной отправке формы вызовем мутацию для очистки корзины
  this.$store.commit('updateOrderInfo', response.data); // при успешной отправке формы вызовем мутацию для записи информации о заказе в глобальное хранилище
  this.$router.push({name:'orderInfo', params: {id: response.data.id}});
})
.catch(error => {  // обработаем ошибку
  this.formError = error.response.data.error.request || {}; // в обьект formError запишем ответ обьекта с ошибками который приходит из сервера
  this.formErrorMessage = error.response.data.error.message; // при возникновении ошибки запишем сообщение в formErrorMessage
})
    }
  }
}
</script>
