import Vue from 'vue';
import VueRouter from 'vue-router';
import MainPage from '@/pages/MainPage';
import ProductPage from '@/pages/ProductPage';
import CartPage from '@/pages/CartPage';
import OrderPage from '@/pages/OrderPage'
import OrderInfoPage from '@/pages/OrderInfoPage';
import NotFoundPage from '@/pages/NotFoundPage';

Vue.use(VueRouter);  // говорим Vue что будем использовать vue-router

const routes = [  // массив с маршрутами
{ name: 'main', component: MainPage, path: '/'},  //1.свойство название страницы 2.название экземпляра страницы который будет отрисовываться когда страница активна 3.path - путь в адресной строке
{ name: 'product', component: ProductPage, path: '/product/:id' },
{name: 'cart', component: CartPage, path: '/cart'},
{name: 'order', component: OrderPage, path: '/order'},
{name: 'orderInfo', component: OrderInfoPage, path: '/order/:id'},
{name: 'notFound', component: NotFoundPage, path: '*'},
];


const router = new VueRouter({ // создаем новый экземпляр vue-router
routes
});

export default router;   // передаем роутер в main.js

