<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
        {{ countProducts}} товар
      </span>
    </div>

    <div class="content__catalog">
      <ProductFilter :price-from.sync="filterPriceFrom" :price-to.sync="filterPriceTo"
        :category-id.sync="filterCategoryId" :color.sync="filterColorId" />

      <section class="catalog">

        <Loader v-if="productsLoading"></Loader>
        <div v-if="productsLoading">Загрузка товаров...</div>
        <div v-if="productsLoadingFailed">Произошла ошибка при загрузке товаров...
          <button @click.prevent="loadProducts">Попробовать еще раз</button>
        </div>

        <ProductList :products="products" />

        <BasePagination v-model="page" :count="countProducts" :per-page="productsPerPage" />
      </section>
    </div>
  </main>
</template>

<script>

import ProductList from '@/components/ProductList';
import BasePagination from '@/components/BasePagination.vue';
import ProductFilter from '@/components/ProductFilter.vue';
import Loader from '@/components/Loader.vue';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

export default {

  components: { ProductList, BasePagination, ProductFilter, Loader },

  data() {
    return { // свойства фильтра
      filterPriceFrom: 0,  // поле цены фильтрации ОТ
      filterPriceTo: 0,  // поле цены фильтрации ДО
      filterCategoryId: 0, // поле фильтрации КАТЕГОРИИ ТОВАРА
      filterColorId: 0,   // поле выбора ЦВЕТА ТОВАРА в фильтре

      page: 1, // Текушая страница
      productsPerPage: 3,  // Количество товаров на странице

      productsData: null, // свойство, где хранится ответ от сервера
      productsLoading: false,
      productsLoadingFailed: false,  // статус, произошла ошибка при загрузке или нет

    }
  },

  computed: { //Вычисляемые свойства
    products() {
      return this.productsData ? this.productsData.items.map(product => {
        return {
          ...product,
          image: product.image.file.url, // путь до картинки
        }
      }) : [];   // возвращаем товары из состояния productsData, которые приходят из сервера (если их нет - получим пустой [])
    },

    countProducts() {
      // return this.filteredProducts.length; // Вернет общее количество товаров
      return this.productsData ? this.productsData.pagination.total : 0;
    },

  },

  methods: {
    // метод загрузки товаров с сервера
    loadProducts() {
      this.productsLoading = true; //при загрузке товаров сбрасываем это свойство
      this.productsLoadingFailed = false; // при запросе на сервер ставим статус false
      clearTimeout(this.loadProductTimer);
      this.loadProductTimer = setTimeout(() => {
        axios.get(API_BASE_URL + '/api/products', {
          params: {
            page: this.page,
            limit: this.productsPerPage,
            categoryId: this.filterCategoryId,
            minPrice: this.filterPriceFrom,
            maxPrice: this.filterPriceTo,
            colorId: this.filterColorId,
          }
        })
          // если запрос успешен, ответ запишем в переменную в состоянии productsData
          .then(response => this.productsData = response.data)
          .catch(() => this.productsLoadingFailed = true)
          .then(() => this.productsLoading = false);

      }, 2500);
    },
  },

  watch: {
    page() { // наблюдаем за свойством page и если оно изменилось вызываем метод loadProducts
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterColorId() {
      this.loadProducts();
    }
  },
  created() {
    this.loadProducts();
  }
}
</script>
