<template>
<li class="cart__item product">
              <div class="product__pic">
                <img :src="item.product.image" width="120" height="120" alt="item.product.title">
              </div>
              <h3 class="product__title">
                {{item.product.title}}
              </h3>
              <p class="product__info product__info--color">
                Цвет:
                <span >
                  <!-- <i :style="{ backgroundColor: item.product.colors.code }"></i> -->
                  Нежно-голубой
                </span>
              </p>
              <span class="product__code">
                Артикул: {{ item.product.id }}
              </span>

              <Counter v-model="amount"/>

              <b class="product__price">
                {{ (item.amount * item.product.price) | numberFormat }} ₽
              </b>

              <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины" @click.prevent="deleteProductToCart(item.productId)">
                <svg width="20" height="20" fill="currentColor">
                  <use xlink:href="#icon-close"></use>
                </svg>
              </button>
            </li>

</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import { mapActions} from 'vuex';
import Counter from '@/components/Counter.vue';


export default {
props:['item', 'color'],
components: {Counter},
filters: {numberFormat},

computed: {
  amount:{
get(){
  return this.item.amount;
},
set(value){  // метод set вызывает мутацию
   return this.$store.dispatch('updateCartProductAmount', {productId: this.item.productId, amount: value});
  }
  }
},

methods: {
    ...mapActions(['deleteProductToCart']),
  }
}

</script>
