<template>
<ul class="catalog__pagination pagination">

<li class="pagination__item">
  <a class="pagination__link pagination__link--arrow" href="#" :class="{'pagination__link--disabled': page === 1}" aria-label=" Предыдущая страница" @click.prevent="_$event=>prevPage(page)">
    <svg width="8" height="14" fill="currentColor">
      <use xlink:href="#icon-arrow-left"></use>
    </svg>
  </a>
</li>

<li class="pagination__item" v-for="pageNumber in pages" :key="pageNumber">
  <a href="#" class="pagination__link" :class="{'pagination__link--current': pageNumber === page}" @click.prevent="_$event=>paginate(pageNumber)">
   {{pageNumber}}
  </a>
</li>

<li class="pagination__item">
  <a href="#" class="pagination__link" :class="{'pagionation__link--disabled': page === pages}" aria-label="Следующая страница" @click.prevent="_$event=>nextPage(page)">
    <svg width="8" height="14" fill="currentColor">
      <use xlink:href="#icon-arrow-right"></use>
    </svg>
  </a>
</li>
</ul>
</template>

<script>
export default {

  model: {
    prop: 'page',
    event: 'paginate',
  },

  props: ['page', 'count', 'perPage'], // входящий параметр(значение текущей страницы,общее кол-во элементов,кол-во элементов на странице)

  computed:{
    pages(){
      return Math.ceil(this.count / this.perPage); // Округляем значение в большую сторону
    },
  },
  methods: {
      paginate(page){
        this.$emit('paginate', page);
      },
      prevPage(page){
        if(page > 1){
          this.$emit('paginate', page - 1);
        }
      },
      nextPage(page){
if(page < this.pages){
  this.$emit('paginate', page + 1);
}
      },
  },

}
</script>
