<template>
<div class="form__counter">
                <button type="button" aria-label="Убрать один товар" href="#" :disabled="amount===1" @click="counterDecrease"  >
                  <svg width="12" height="12" fill="currentColor">
                    <use xlink:href="#icon-minus"></use>
                  </svg>
                </button>

                <input type="text" :value="amount" @input="$emit('update-amount', +$event.target.value)">

                <button type="button" aria-label="Добавить один товар" href="#" :disabled="amount > 20" @click="counterIncrease" >
                  <svg width="12" height="12" fill="currentColor">
                    <use xlink:href="#icon-plus"></use>
                  </svg>
                </button>
              </div>

</template>

<script>
export default {
model:{
prop: 'amount',
event: 'update-amount'
},
props:['amount'],

methods:{
counterDecrease(){
if(this.amount > 1){
  this.$emit('update-amount', this.amount - 1);
}
},
counterIncrease(){
this.$emit('update-amount', this.amount + 1)
},
}
}
</script>
